:root {
  --ff-body: "Hanken Grotesk", sans-serif;
}

:root {
  --Light-red-li: hsl(0, 100%, 67%);
  --light-red-dm: hsl(4, 100%, 74%);
  --yellow-li: hsl(39, 100%, 56%);
  --yellow-dm: hsl(38, 100%, 63%);
  --green-li: hsl(166, 100%, 37%);
  --green-dm: hsl(160, 52%, 59%);
  --blue-li: hsl(234, 85%, 45%);
  --blue-dm: hsl(256, 23%, 76%);
  --main-clr-li: hsl(222, 100%, 96%);
  --main-clr-dm: hsl(216, 3%, 90%);
  --secondary-clr-li: #c8c7ff;
  --secondary-clr-dm: #9d9da1;
  --third-clr-li: hsl(224, 30%, 27%);
  --third-clr-dm: hsl(227, 21%, 67%);
  --bg-clr-li: #7857ff;
  --bg-clr-dm: #46347e;
  --bg-clr-fcs-li: #2e2be9;
  --bg-clr-fcs-dm: #2d2175;
  --circle-clr-li: #4e21ca;
  --circle-clr-dm: #341d67;
  --circle-clr-fcs-li: #2421ca00;
  --circle-clr-fcs-dm: #281c6700;
  --white-li: hsl(0, 0%, 100%);
  --white-dm: hsl(0, 0%, 8%);
  --black-li: hsl(0, 0%, 8%);
  --black-dm: hsl(0, 0%, 100%);
  --about-clr-li: hsl(0, 0%, 95%);
  --about-clr-dm: hsla(0, 0%, 15%, 1);
  --logo-bg-li: hsl(0, 0%, 10%);
  --logo-bg-dm: hsla(0, 0%, 95%, 1);
  --logo-clr-li: hsl(119, 50%, 39%);
  --logo-clr-dm: hsl(119, 70%, 58%);
  --about-clr: var(--about-clr-li);
  --logo-bg: var(--logo-bg-li);
  --logo-clr: var(--logo-clr-li);
  --main-clr: var(--main-clr-li);
  --secondary-clr: var(--secondary-clr-li);
  --third-clr: var(--third-clr-li);
  --bg-clr: var(--bg-clr-li);
  --bg-clr-fcs: var(--bg-clr-fcs-li);
  --circle-clr: var(--circle-clr-li);
  --circle-clr-fcs: var(--circle-clr-fcs-li);
  --light-red: var(--Light-red-li);
  --yellow: var(--yellow-li);
  --green: var(--green-li);
  --blue: var(--blue-li);
  --white: var(--white-li);
  --black: var(--black-li);
}

.dark-mode {
  --main-clr: var(--main-clr-dm);
  --secondary-clr: var(--secondary-clr-dm);
  --third-clr: var(--third-clr-dm);
  --bg-clr: var(--bg-clr-dm);
  --bg-clr-fcs: var(--bg-clr-fcs-dm);
  --circle-clr: var(--circle-clr-dm);
  --circle-clr-fcs: var(--circle-clr-fcs-dm);
  --light-red: var(--light-red-dm);
  --yellow: var(--yellow-dm);
  --green: var(--green-dm);
  --blue: var(--blue-dm);
  --white: var(--white-dm);
  --black: var(--black-dm);
  --about-clr: var(--about-clr-dm);
  --logo-bg: var(--logo-bg-dm);
  --logo-clr: var(--logo-clr-dm);
}
.dark-mode .card {
  box-shadow: 13px 13px 28px 0px rgba(235, 241, 255, 0.1);
}
.dark-mode .card button {
  color: var(--black);
}
.dark-mode img {
  filter: saturate(20) contrast(200%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}
html:focus-within {
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  font: inherit;
}

body {
  margin: 0;
  height: 100%;
  padding: 0;
  text-rendering: optimizeSpeed;
  font-family: var(--ff-body);
  overflow-x: hidden;
}

.center {
  display: grid;
  align-items: center;
  height: 100%;
  position: relative;
}

h1,
h2,
h3,
blockquote,
p {
  margin: 0;
}

a,
a:visited,
a:active {
  color: whitesmoke;
  text-decoration: none;
}

ul,
li {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

img {
  display: block;
  object-fit: cover;
  max-width: 100%;
}

.flex {
  display: flex;
  align-items: center;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
@keyframes scale-left-logo {
  0% {
    max-width: 0rem;
    font-size: 0rem;
  }
  100% {
    font-size: 0.9375rem;
    max-width: 28.125rem;
  }
}
.btns {
  position: absolute;
  width: 100%;
  top: 3%;
  right: 0;
}
.btns button {
  position: absolute;
  font-size: 1.1875rem;
  top: 3%;
  border: none;
  background-color: transparent;
  text-transform: capitalize;
  cursor: pointer;
  color: var(--logo-clr);
  font-weight: bold;
}
.btns .dark-mode-btn {
  right: 2%;
}
.btns span {
  display: none;
}

#remove-events {
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
}

.center {
  z-index: -2;
  height: 100%;
  display: grid;
  align-items: center;
  justify-content: center;
}

body {
  background-color: var(--white);
  z-index: -5;
  position: relative;
}

main {
  display: block;
}

section {
  max-width: 46rem;
  display: grid;
  justify-content: center;
  gap: 1.5625rem 0;
}
section .button {
  margin-bottom: 1.875rem;
  align-self: center;
}
section .button button {
  text-align: center;
  width: 19.6875rem;
  background-color: var(--third-clr);
  padding: 1.75rem 0;
  border-width: 0;
  color: var(--white);
  border-radius: 50px;
  font-size: 1.125rem;
  letter-spacing: -0.2px;
  cursor: pointer;
}

.card-1 {
  background: linear-gradient(0deg, var(--bg-clr-fcs) 0%, var(--bg-clr) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1.75rem 0;
  border-radius: 0 0 25px 25px;
  padding: 1.5625rem;
}
.card-1--circle {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.65625rem 2.625rem;
  background: linear-gradient(0deg, var(--circle-clr-fcs) 5%, var(--circle-clr) 100%);
  border-radius: 50%;
}
.card-1--circle h1 {
  font-size: 3.125rem;
  font-weight: 800;
}
.card-1--circle p {
  opacity: 0.5;
}
.card-1 h1 {
  color: var(--main-clr);
}
.card-1 p {
  color: var(--secondary-clr);
  font-size: 1.125rem;
  font-weight: 700;
}
.card-1--desc {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.9375rem 0;
  margin-bottom: 0.9375rem;
}
.card-1--desc h1 {
  font-size: 1.375rem;
  font-weight: 700;
}
.card-1--desc p {
  opacity: 0.9;
  max-width: 30ch;
}

.card-2 {
  display: flex;
  flex-direction: column;
  gap: 1.625rem 0;
  margin: 0 1.875rem;
  background-color: var(--white);
}
.card-2--header h2 {
  font-size: 1.25rem;
  font-weight: 800;
  letter-spacing: -0.5px;
  color: var(--third-clr);
}
.card-2 ul {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-items: center;
  row-gap: 0.9375rem;
}
.card-2 ul li {
  display: flex;
  justify-content: space-between;
  max-width: 19.6875rem;
  height: 3.5rem;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
  border-radius: 10px;
  padding: 1rem;
}
.card-2 ul li div {
  display: flex;
  gap: 0 0.75rem;
}
.card-2 ul li h3 {
  font-size: 1.125rem;
}
.card-2 ul li p {
  color: rgba(48, 59, 90, 0.6);
  font-weight: 800;
}
.card-2 ul li span {
  color: var(--third-clr);
}
.card-2 ul li:nth-child(1) {
  background-color: rgba(255, 87, 87, 0.07);
}
.card-2 ul li:nth-child(1) h3 {
  color: var(--light-red);
}
.card-2 ul li:nth-child(2) {
  background-color: rgba(255, 176, 31, 0.07);
}
.card-2 ul li:nth-child(2) h3 {
  color: var(--yellow);
}
.card-2 ul li:nth-child(3) {
  background-color: rgba(0, 189, 145, 0.07);
}
.card-2 ul li:nth-child(3) h3 {
  color: var(--green);
}
.card-2 ul li:nth-child(4) {
  background-color: rgba(17, 37, 212, 0.07);
}
.card-2 ul li:nth-child(4) h3 {
  color: var(--blue);
}

@media (min-width: 50em) {
  .card {
    grid-template-columns: repeat(auto-fit, 23rem);
    border-radius: 30px;
    box-shadow: 13px 13px 28px 0px var(--main-clr);
  }
  .card-1 {
    border-radius: 30px;
    opacity: 0.85;
    padding: 3.125rem;
    padding-top: 2.75rem;
  }
  .card-1--circle {
    padding: 2.4375rem 3.59rem;
  }
  .card-1--circle h1 {
    font-size: 4.75rem;
  }
  .card-1--desc {
    gap: 1.25rem 0;
    margin-bottom: 0.875rem;
  }
  .card-1--desc h1 {
    font-size: 1.75rem;
  }
  .card-1--desc p {
    max-width: 25ch;
  }
  .card-2 {
    gap: 0;
    margin: 0;
    border-radius: 0 30px 30px 0;
    padding: 1.25rem 2.5rem;
  }
  .card-2--header {
    margin-top: 1.25rem;
  }
  .card-2 ul {
    margin-top: 1.875rem;
    margin-bottom: 2.5rem;
    row-gap: 1rem;
  }
  .card-2 ul li {
    max-width: 18rem;
    height: 3.5rem;
    padding: 1.125rem;
  }
  .card-2 .button {
    margin-bottom: 0rem;
  }
  .card-2 .button button {
    max-width: 18rem;
    opacity: 0.8;
    padding: 1.25rem 0;
    font-size: 1.25rem;
    font-weight: 700;
  }
  .card-2 .button button:hover {
    background: linear-gradient(0deg, var(--bg-clr-fcs) 0%, var(--bg-clr) 100%);
    opacity: 1;
  }
}
.attribution {
  font-size: 0.9375rem;
  text-align: center;
  background-color: var(--logo-bg);
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  max-width: fit-content;
  color: var(--about-clr);
  border-radius: 2.5rem;
  gap: 0.9375rem;
  margin-inline: 1.375rem;
  position: fixed;
  bottom: 2%;
  right: 1%;
}
.attribution #about {
  margin-left: 0.9375rem;
  animation: scale-left-logo 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.attribution #about.none {
  display: none;
}
.attribution #logo {
  cursor: pointer;
}
.attribution #logo img {
  border-radius: 50%;
  max-width: 5rem;
}
.attribution a {
  color: var(--logo-clr);
  font-weight: bold;
}

.none {
  display: none;
}